import {IframeClientTransport} from './iframe-client-transport';

export const STUDIO_SET_VIEW_STATE = 'map-sdk-set-view-state';
export const STUDIO_GET_LAYERS = 'map-sdk-get-layers';
export const STUDIO_SET_LAYER_VISIBILITY = 'map-sdk-set-layer-visibility';
export const STUDIO_SET_THEME = 'map-sdk-set-theme';
export const STUDIO_MAP_INITIALIZED = 'map-sdk-initialized';
export const STUDIO_GET_TIMELINE_INFO = 'map-sdk-get-timeline-info';
export const STUDIO_TOGGLE_TIMELINE_ANIMATION = 'map-sdk-toggle-timeline-animation';
export const STUDIO_TOGGLE_TIMELINE_VISIBILITY = 'map-sdk-toggle-timeline-visibility';
export const STUDIO_SET_TIMELINE_INTERVAL = 'map-sdk-set-timeline-interval';
export const STUDIO_TIMELINE_INTERVAL_CHANGED = 'map-sdk-timeline-interval-changed';
export const STUDIO_SET_TIMELINE_SPEED = 'map-sdk-set-timeline-speed';
export const STUDIO_SET_TIMELINE_CONFIG = 'map-sdk-set-timeline-config';
export const STUDIO_REFRESH_MAP_DATA = 'map-sdk-reload-map-data';
export const STUDIO_GET_LAYER_TIMELINE_INFO = 'map-sdk-get-layer-timeline-info';
export const STUDIO_LAYER_ANIMATION_TIME_CHANGED = 'map-sdk-layer-animation-time-changed';
export const STUDIO_SET_LAYER_TIMELINE_CONFIG = 'map-sdk-set-layer-timeline-config';
export const STUDIO_SET_MAP_CONTROL_VISIBILITY = 'map-sdk-set-map-control-visibility';
export const STUDIO_ADD_TILESET_TO_MAP = 'map-sdk-add-tileset-top-map';

export function getMapUrl(mapUUID) {
  return `https://studio.unfolded.ai/public/${mapUUID}`;
}

export function createMap({
  mapUUID,
  mapUrl,
  width,
  height,
  id,
  appendToDocument,
  appendToId,
  embed,
  onLoad,
  onTimelineIntervalChange,
  onLayerTimelineTimeChange
}) {
  if (mapUUID) {
    mapUrl = getMapUrl(mapUUID);
  }
  return new IframeClientTransport({
    url: mapUrl,
    width,
    height,
    id,
    appendToDocument,
    appendToId,
    embed,
    onLoad,
    onTimelineIntervalChange,
    onLayerTimelineTimeChange
  });
}

export function setViewState(map, viewState) {
  return map.sendMessage(STUDIO_SET_VIEW_STATE, {viewState});
}

export function getLayers(map) {
  return map.sendMessage(STUDIO_GET_LAYERS, {});
}

export function setLayerVisibility(map, layerId, isVisible) {
  return map.sendMessage(STUDIO_SET_LAYER_VISIBILITY, {layerId, isVisible});
}

export function setTheme(map, theme) {
  return map.sendMessage(STUDIO_SET_THEME, {theme});
}

export function getTimelineInfo(map, idx) {
  return map.sendMessage(STUDIO_GET_TIMELINE_INFO, {idx});
}

export function toggleTimelineAnimation(map, idx) {
  return map.sendMessage(STUDIO_TOGGLE_TIMELINE_ANIMATION, {idx});
}

export function toggleTimelineVisibility(map, idx) {
  return map.sendMessage(STUDIO_TOGGLE_TIMELINE_VISIBILITY, {idx});
}

export function setTimelineInterval(map, idx, startTime, endTime) {
  return map.sendMessage(STUDIO_SET_TIMELINE_INTERVAL, {idx, startTime, endTime});
}

export function setTimelineAnimationSpeed(map, idx, speed) {
  return map.sendMessage(STUDIO_SET_TIMELINE_SPEED, {idx, speed});
}

export function setTimelineConfig(map, config) {
  return map.sendMessage(STUDIO_SET_TIMELINE_CONFIG, {config});
}

export function refreshMapData(map) {
  return map.sendMessage(STUDIO_REFRESH_MAP_DATA, {});
}

export function getLayerTimelineInfo(map) {
  return map.sendMessage(STUDIO_GET_LAYER_TIMELINE_INFO, {});
}

export function setLayerTimelineConfig(map, config) {
  return map.sendMessage(STUDIO_SET_LAYER_TIMELINE_CONFIG, {config});
}

export function setMapControlVisibility(map, config) {
  return map.sendMessage(STUDIO_SET_MAP_CONTROL_VISIBILITY, {config});
}

export function _addTileset(map, tileset) {
  return map.sendMessage(STUDIO_ADD_TILESET_TO_MAP, {tileset});
}
