/* global window, document */

const SOURCE_CLIENT = 'unfolded-sdk-client';
const SOURCE_SERVER = 'unfolded-sdk-server';

/**
 * A wrapper for an iframe that provided facilities for sending and receiving messages
 */
export class IframeClientTransport {
  /**
   * Creates an iframe from the provided URL and calls the provided `onMessage` callback
   * with messages from the iframe.
   * @param {object} options
   * @param {string} options.url url to open inside the new iframe
   * @param {string} [options.id] the DOM id of the new iframe
   * @param {string} [options.appendToDocument] is iframe appended to the DOM or not
   * @param {string} [options.appendToId] the id of the element to append the iframe to, defaults to body
   * @param {*} [options.width] the width of the newly created iframe
   * @param {*} [options.height] the height of the newly created iframe
   * @param {boolean} [options.embed] should the map be ebedded (with hidden side menu) or not
   * @param {function} options.onLoad handler triggered once the map loads
   * @param {function} options.onTimelineIntervalChange handler triggered whenever the timeline interval changes
   * @param {function} options.onLayerTimelineTimeChange handler triggered whenever the layer timeline timestamp changes
   */
  constructor(options) {
    const url = options.url;
    const id = options.id || 'unfolded_iframe';
    const appendToDocument = options.appendToDocument;
    const appendToId = options.appendToId || '';
    const width = options.width || 400;
    const height = options.height || 250;
    const embed = options.embed || false;
    const onLoad = options.onLoad;
    const onTimelineIntervalChange = options.onTimelineIntervalChange;
    const onLayerTimelineTimeChange = options.onLayerTimelineTimeChange;

    window.addEventListener('message', event => {
      onMessage(
        event,
        this.type,
        this.resolve,
        this.reject,
        onLoad,
        onTimelineIntervalChange,
        onLayerTimelineTimeChange
      );
    });

    this.iframe = document.createElement('iframe');
    setupIFrame(this.iframe, url, embed, id, appendToDocument, appendToId, width, height);
  }

  /**
   * Send a message to the child iframe
   * @param {string} type
   * @param {object} data
   */
  sendMessage(type, data) {
    const iframe = this.iframe;
    const promise = new Promise((resolve, reject) => {
      // Make sure we are sending a string, and to stringify JSON
      const source = SOURCE_CLIENT;
      const string = JSONStringifySafe({type, source, data});
      // @ts-ignore
      iframe.contentWindow.postMessage(string, '*');
      this.type = type;
      this.resolve = resolve;
      this.reject = reject;
    });

    return promise;
  }
}

// eslint-disable-next-line max-params
function setupIFrame(iframe, url, embed, id, appendToDocument, appendToId, width, height) {
  if (embed) {
    url += '/embed';
  }
  iframe.setAttribute('src', url);
  iframe.setAttribute('id', id);
  if (width) {
    iframe.style.width = `${width}px`;
  }
  if (height) {
    iframe.style.height = `${height}px`;
  }
  if (appendToDocument) {
    if (appendToId && document.getElementById(appendToId)) {
      const containerElement = document.getElementById(appendToId);
      if (containerElement) {
        containerElement.appendChild(iframe);
      }
    } else {
      document.body.appendChild(iframe);
    }
  }
}

/**
 * Resolves or rejects promise based on the type of event received from the server
 * @param {object} event
 * @param {string | undefined} type
 * @param {*} resolve
 * @param {*} reject
 * @param {function} onLoad
 * @param {function} onTimelineIntervalChange
 */
// eslint-disable-next-line max-params
function onMessage(
  event,
  type,
  resolve,
  reject,
  onLoad,
  onTimelineIntervalChange,
  onLayerTimelineTimeChange
) {
  const json = JSON.parse(event.data);
  if (json.source === SOURCE_SERVER) {
    const typeResponse = json.type;
    const dataResponse = json.data;
    switch (typeResponse) {
      case 'map-sdk-initialized-response':
        if (onLoad) {
          onLoad();
        }
        break;
      case 'map-sdk-timeline-interval-changed-response':
        if (onTimelineIntervalChange) {
          onTimelineIntervalChange(dataResponse);
        }
        break;
      case 'map-sdk-layer-animation-time-changed-response':
        if (onLayerTimelineTimeChange) {
          onLayerTimelineTimeChange(dataResponse);
        }
        break;
      case `${type}-response`:
        resolve(dataResponse);
        break;
      case `${type}-error`:
        reject(new Error(dataResponse.message));
        break;
      default:
        reject(new Error(dataResponse.message));
    }
  }
}

function JSONStringifySafe(v) {
  const cache = new Set();
  return JSON.stringify(v, function(key, value) {
    if (typeof value === 'object' && value !== null) {
      if (cache.has(value)) {
        // Circular reference found
        try {
          // If this value does not reference a parent it can be deduped
          return JSON.parse(JSON.stringify(value));
        } catch (err) {
          // discard key if value cannot be deduped
          return undefined;
        }
      }
      // Store value in our set
      cache.add(value);
    }
    return value;
  });
}
